.wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

button {
  letter-spacing: .2em;
  cursor: pointer;
  background-color: #4169e1;
  border: none;
  border-radius: 2em;
  margin: .5em;
  padding: 1em 2em;
  font-size: 24px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, .5);
}

button[disabled] {
  color: rgba(255, 255, 255, .3);
  background-color: rgba(65, 105, 225, .3);
}

